      justify-content: space-around;
}
.form-group {
  padding: 3px;
  width: 30%;
}
.criterion-name {
  height: 30px;
  width: 90%;
  border: solid #86add8 1px;
  border-radius: 5px;
  font-size: 20px;
}
.button {
  display: block;
  margin: 0 auto;
  margin-top: 50px;
  width: 300px;
  height: 40px;
  background: #3f90ea;
  color: #FFF;
  border: none;
  border-radius: 5px;
  font-size: 20px;
  cursor: pointer;
}
.button.result, .button.back {
  background: #9273ca;
}
.button.clear {
  background: #ffa52b;
}
.vddl-list, .vddl-draggable {
