}
.vddl-list {
  min-height: 44px;
}
.vddl-dragging{
  opacity: 0.7;
}
.vddl-dragging-source {
  display: none;
}
.options {
  text-align: center;
  width: 350px;
  /*border: 1px black solid;*/
  list-style: none;
}
.options .form-group {
  width: 100%;
}
.options .form-group input {
  width: calc(100% - 40px);
}
.options .heightForm input{
  width: calc(100% - 150px);
}
.lockButton {
  width: 90%;
  height: 30px;
  color: #FFF;
  margin: 0 auto;
  margin-top: 20px;
}
.addButton {
  width: 90%;
  height: 30px;
  color: #FFF;
  margin: 0 auto;
  margin-top: 20px;
  background: #3c63c5;
}
.result-table {
  width: 70%;
  margin: 0 auto;
  margin-top: 50px;
}
/*.result-bar-wrap {
  width: 80%;
}*/
.result-bar {
  height: 20px;
  background: #3f90ea;
}

.ctx {
  position: relative;
}
.ctx-menu {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1000;
  display: none;
  float: left;
  min-width: 160px;
  padding: 5px 0;
  margin: 2px 0 0;
  font-size: .9rem;
  color: #373a3c;
  text-align: left;
  list-style: none;
  background-color: #fff;
  background-clip: padding-box;
  /*border: 1px solid rgba(0, 0, 0, .15);*/
  border-radius: .25rem; -webkit-box-shadow:0 0 5px #CCC; box-shadow:0 0 5px #CCC
}
.ctx-divider {
  height: 1px;
  margin: .5rem 0;
  overflow: hidden;
  background-color: #e5e5e5;
}
.ctx-item {
  display: block;
  /*width: 100%;*/
  padding: 3px 20px;
  clear: both;
  font-weight: normal;
  line-height: 1.5;
  color: #373a3c;
  text-align: inherit;
  white-space: nowrap;
  background: none;
  border: 0;
  cursor: default;
}
.ctx-item:focus,
.ctx-item:hover {
  color: #2b2d2f;
  text-decoration: none;
  background-color: #f5f5f5;
  cursor: normal;
}
.ctx-item.active,
.ctx-item.active:focus,
.ctx-item.active:hover {
  color: #fff;
  text-decoration: none;
  background-color: #0275d8;
  outline: 0;
}
.ctx-item.disabled,
.ctx-item.disabled:focus,
.ctx-item.disabled:hover {
  color: #818a91;
}
.ctx-item.disabled:focus,
.ctx-item.disabled:hover {
  text-decoration: none;
  cursor: not-allowed;
  background-color: transparent;
  background-image: none;
  filter: "progid:DXImageTransform.Microsoft.gradient(enabled = false)";
}
.open > .ctx-menu {
  display: block;
}
.open > a {
  outline: 0;
}
.ctx-menu-right {
  right: 0;
  left: auto;
}
.ctx-menu-left {
  right: auto;
  left: 0;
}
.ctx-header {
  display: block;
  padding: 3px 20px;
  font-size: .9rem;
  line-height: 1.5;
  color: #818a91;
  white-space: nowrap;
}
.ctx-backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 990;
}
.pull-right > .ctx-menu {
  right: 0;
  left: auto;
}
.ctx-menu-container {
  position: fixed;
  padding: 0;
  border: 1px solid #bbb;
  background-color: whitesmoke;
  z-index: 99999;
  -webkit-box-shadow: 0 5px 11px 0 rgba(0, 0, 0, 0.18), 0 4px 15px 0 rgba(0, 0, 0, 0.15);
          box-shadow: 0 5px 11px 0 rgba(0, 0, 0, 0.18), 0 4px 15px 0 rgba(0, 0, 0, 0.15);
}

